<template>
  <div class="pageContent">
    <el-card shadow="always" style="margin-bottom:20px;">
      <div class="pageHead">活动基本信息</div>
      <el-form ref="form" :model="form" label-width="140px">
        <el-form-item label="活动名称：" required>
          <el-input v-model.trim="form.name" style="width:400px;" maxlength="20" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="活动开始时间：" required>
          <el-date-picker
            :disabled="form.activityStatus == 1"
            v-model="form.beginTime"
            value-format="yyyy-MM-dd HH:mm:ss"
            type="datetime"
            placeholder="选择开始时间">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="活动结束时间：" required>
          <el-date-picker
            v-model="form.endTime"
            value-format="yyyy-MM-dd HH:mm:ss"
            type="datetime"
            placeholder="选择结束时间"
            :picker-options="pickerOptions">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="重要提醒：" required>
          <div>限时折扣活动与砍价活动互斥</div>
        </el-form-item>
        <el-form-item label="活动限购：" required>
          <el-input-number :disabled="form.activityStatus == 1" v-model="form.limitNumber" :min="0" :max="9999" step-strictly :controls="false" placeholder="请输入0~9999"></el-input-number>
          <span style="color:#909399;margin-left:20px;">单个商品活动期间内限购数量，输入0代表不限购</span>
        </el-form-item>
        <el-form-item label="分享朋友圈标题：">
          <el-input v-model.trim="form.wxMomentsTitle" style="width:400px;" maxlength="20" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="分享好友标题：">
          <el-input v-model.trim="form.wxFriendsTitle" style="width:400px;" maxlength="20" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="分享好友文案：">
          <el-input v-model.trim="form.wxFriendsContent" style="width:400px;" maxlength="30" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="分享示例：">
          <el-button type="primary" @click="showImg = true">查看示例</el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <el-card shadow="always">
      <div class="flex-row-between" style="width:100%;margin-bottom:20px;">
        <div>
          <el-button :disabled="form.activityStatus == 1" @click="showCom" type="primary" icon="el-icon-circle-plus-outline">新增活动商品</el-button>
        </div>
        <div class="flex-row-start" v-show="commodityList.length > 0" style="padding-right:20px;">
          <el-radio-group v-model="area">                    
            <el-radio :disabled="form.activityStatus == 1" @click.native.prevent="clickitem(item.AreaName,item.type)"  :label="item.AreaName"  v-for="(item,index) in areaItem" :key="index">{{item.AreaName}}</el-radio>
          </el-radio-group>
          <el-tooltip class="item ml" effect="dark" content="抹零是指付钱时不计整数以外的尾数；抹零对折后价低于1.00的商品无效；" placement="top">
            <i class="el-icon-question"></i>
          </el-tooltip>
        </div>
      </div>
      <div>
        <el-table 
          v-loading="formLoading"
          ref="multipleTable" 
          :data="commodityList" 
          tooltip-effect="dark" 
          style="width: 100%" 
          @selection-change="handleSelectionChange"
          :row-class-name="getRowClassName">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-table :data="props.row.list" border style="width: 100%">
                <el-table-column prop="skuName" label="规格"></el-table-column>
                <el-table-column prop="costPrice" label="成本价（元）"></el-table-column>
                <el-table-column prop="price" label="商品原价（元）"></el-table-column>
                <el-table-column prop="finallyPrice" label="折后价格（元）">
                  <template slot-scope="scope">{{ scope.row.finallyPrice == 0 ? scope.row.price : scope.row.finallyPrice}}</template>
                </el-table-column>
                <el-table-column prop="skuStock" label="库存"></el-table-column>
              </el-table>
            </template>
          </el-table-column>
          <el-table-column label="商品名称" prop="productName"></el-table-column>
          <el-table-column label="商品库存" prop="productStock" width="140"></el-table-column>
          <el-table-column width="140">
            <template slot="header" slot-scope="scope">
              <span :data-scope="scope">商品已售</span>
              <el-tooltip class="item" effect="dark" content="商品设置初始已抢数量，促进活动气氛，推荐填写。" placement="top">
                <i class="el-icon-question"></i>
              </el-tooltip>
            </template>
            <template slot-scope="scope">
              <el-input-number style="width:100px;" v-model="scope.row.productFinallyTotal" step-strictly :controls="false" :min="0" :max="100000"></el-input-number>
            </template>
          </el-table-column>
          <el-table-column label="成本价（元）" width="140">
            <template slot-scope="scope">{{ scope.row.costPrice ? '￥'+scope.row.costPrice : '-' }}</template>
          </el-table-column>
          <el-table-column label="商品售价（元）" width="140">
            <template slot-scope="scope">￥{{ scope.row.minPrice }}</template>
          </el-table-column>
          <el-table-column label="商品折扣" width="140">
            <template slot-scope="scope">
              <el-input-number :disabled="form.activityStatus == 1" @blur="itemBlur(scope)" style="width:100px;" v-model="scope.row.discount" step-strictly :controls="false" :min="1" :max="99"></el-input-number>%
            </template>
          </el-table-column>
          <el-table-column label="折后价格" width="140">
            <template slot-scope="scope">￥{{ scope.row.finallyPrice == 0 ? scope.row.minPrice : scope.row.finallyPrice}}</template>
          </el-table-column>
          <el-table-column label="操作" width="100">
            <template slot-scope="scope">
              <el-button :disabled="form.activityStatus == 1" @click="deleteItem(scope.$index)" type="text" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="flex-row-start" style="margin-top:20px" v-if="form.activityStatus == 0">
          <el-button style="margin-right:20px;" @click="deleteAll">批量删除</el-button>
          <el-popover placement="right" trigger="click" v-model="visible">
            <div class="flex-row-start" style="margin-left:20px;">
              <el-input-number style="width:100px;" v-model="alldiscount" step-strictly :controls="false" :min="1" :max="99"></el-input-number>%
              <el-button type="text" style="margin:0 20px;" @click="allChange">确定</el-button>
            </div>
            <el-button slot="reference">批量打折</el-button>
          </el-popover>
        </div>
      </div>
    </el-card>
    <div class="flex-row-center" style="margin-top:20px;">
      <el-button @click="back">返 回</el-button>
      <el-button :loading="subLoading" type="primary" @click="subForm">保 存</el-button>
    </div>
    <!-- 商品列表弹出框 -->
    <commodity :commodityShow="commodityShow" :commodityIds="commodityIds" @commodityClose="commodityClose" @commodityFun="commodityFun"></commodity>
    <el-dialog title="示例" :visible.sync="showImg" width="30%" center>
      <div class="flex-row-center">
        <img style="max-width:100%;" src="../../../assets/images/marketManage/share.png" alt="">
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import commodity from '@/components/popover/commodity';
import { calculatedActivityDiscount , updateShopActivity , getShopActivityById} from '@/api/marketManage/discount'
export default {
  data(){
    return{
      // 默认数据
      area:'',
      areaItem:[
        {AreaName: "抹去角和分", type: 1},
        {AreaName: "抹去分", type: 2},
      ],
      form:{
        time:[],
        calculateType:0
      },            //  新增数据
      radio:'',
      multipleSelection:[],
      showImg:false,
      // 商品弹出框
      commodityShow:false,      // 弹出框是否展示
      commodityIds:[],                   // 弹出框已选择的商品ids
      // 弹出框选择后的商品  
      commodityList:[],
      formLoading:false,
      visible:false,
      alldiscount:undefined,
      showChange:false,
      subLoading:false,
      pickerOptions: {
        // 限制日期
        disabledDate: this.disabledDate
      },
    }
  },
  created(){
    this.getData()
  },
  methods:{
    getData(){
      getShopActivityById({id:this.$route.query.id}).then(res=>{
        console.log('活动详情',res)
        if(res.code == 200){
          this.form = res.data;
          res.data.productList.forEach(e => e.discount == 0 ? e.discount = undefined : e.discount = e.discount)
          this.commodityList = res.data.productList
          if(res.data.calculateType == 1) this.area = '抹去角和分'
          if(res.data.calculateType == 2) this.area = '抹去分'
        }else{
          this.$message.error(res.message);
        }
      })
    },
    deleteAll(){
      if(this.multipleSelection.length == 0){
        this.$message.error('请选择商品后再试');
        return;
      }
      this.multipleSelection.forEach(e=>{
        this.commodityList.splice(this.commodityList.findIndex(item => item.productId === e.productId), 1)
      })
      
    },
    allChange(){
      console.log('multipleSelection',this.multipleSelection)
      if(this.multipleSelection.length == 0){
        this.$message.error('请选择商品后再试');
        return;
      }
      if(!this.alldiscount){
        this.$message.error('请输入有效的折扣');
        return;
      }
      this.multipleSelection.forEach( e => e.discount = this.alldiscount)
      this.alldiscount = undefined
      this.visible = false
      let obj = {
        calculateType:this.form.calculateType
      }
      let arr = []
      this.commodityList.forEach(item => {
        let obj1 = {
          discount:item.discount || '',
          id:item.productId
        }
        arr.push(obj1)
      })
      obj.productList = arr
      this.getPrice(obj,'all')
    },
    back(){
      this.$confirm('确定放弃编辑?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$router.replace({
          path:'/marketManage/discount'
        })
      }).catch(() => {});
    },
    getPrice(e,i){
      this.formLoading = true;
      calculatedActivityDiscount(e).then(res=>{
        console.log('计算价格',res)
        res.data.forEach(item => item.discount == '' ? item.discount = undefined : item.discount )
        if(res.code == 200){
          if(i == 'all'){
            console.log('全选')
            this.commodityList = res.data
          }else{
            console.log('单个',i)
            this.commodityList.splice(i,1,res.data[0])
          }
        }else{
          this.$message.error(res.message);
        }
        this.formLoading = false;
      })
    },
    itemBlur(e){
      if(e.row.discount){
        let obj = {
          calculateType:this.form.calculateType,
          productList : [{
            discount:e.row.discount,
            id:e.row.productId
          }]
        }
        this.getPrice(obj,e.$index)
      }
    },
    clickitem(e,i){
      console.log('e')
      if(this.form.activityStatus != 1){
        if(e===this.area){
          this.area = '' 
          this.form.calculateType = 0
        }else{
          this.area = e   
          this.form.calculateType = i
        }
        console.log('commodityList',this.commodityList)
        // 计算价格
        if(this.commodityList.length > 0){
          let obj = {
            calculateType:this.form.calculateType
          }
          let arr = []
          this.commodityList.forEach(item => {
            let obj1 = {
              discount:item.discount || '',
              id:item.productId
            }
            arr.push(obj1)
          })
          obj.productList = arr
          this.getPrice(obj,'all')
        }
      }
    },
    subForm(){
      console.log('请求的数据',this.form)
      if(!this.form.name){
        this.$message.error('请输入活动名称');
        return;
      }
      if(!this.form.beginTime){
        this.$message.error('请设置活动开始时间');
        return;
      }
      if(!this.form.endTime){
        this.$message.error('请设置活动结束时间');
        return;
      }
      let dateBegin = new Date(this.form.beginTime.replace(/-/g, "/")).getTime() ;
      let dateEnd = new Date(this.form.endTime.replace(/-/g, "/")).getTime() ;
      if(dateEnd <= dateBegin){
        this.$message.error('结束时间应大于开始时间');
        return;
      }
      if(this.form.limitNumber == undefined){
        this.$message.error('请输入限购数');
        return;
      }
      if(this.commodityList.length == 0){
        this.$message.error('请添加参加活动的商品');
        return;
      }
      let errArr = this.commodityList.filter(e => !e.discount)
      if(errArr.length > 0){
        this.$message.error('列表中存在不完整数据');
        return;
      }
      this.subLoading = true
      this.form.productList = this.commodityList
      this.form.shopId = this.shop.shopId
      console.log('errArr',errArr)
      updateShopActivity(this.form).then(res=>{
        console.log('修改活动',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          setTimeout(()=>{
            this.subLoading = false;
            this.$router.replace({
              path:'/marketManage/discount'
            })
          },1000)
        }else{
          this.subLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    deleteItem(i){
      this.commodityList.splice(i,1)
      if(this.commodityList.length == 0){
        this.area = '' 
        this.form.calculateType = 0
      }
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    showCom(){
      let ids = []
      this.commodityList.forEach( e => ids.push(e.productId))
      this.commodityIds = ids
      console.log(this.commodityIds)
      this.commodityShow = true;
    },
    // 弹出框
    commodityClose(){
      this.commodityShow = false;
    },
    commodityFun(e){
      this.commodityShow = false
      e.forEach( item => {
        item.discount = undefined
        this.commodityList.push(item)
      })
    },
    getRowClassName({row, rowIndex}){
      if (row.skuType == 0) {
        return 'row-expand-cover';
      }
    }
  },
  components:{
    commodity
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .pageContent /deep/ .el-range-separator{
    line-height: 24px;
  }
  .ml{
    margin-left: 60px;
  }
  .row-expand-cover td:last-child .el-icon-arrow-right{
    visibility: hidden;
  }
  .row-expand-cover .el-table__expand-column .el-icon{
    visibility:hidden;
  }
</style>
<style>
  .row-expand-cover .el-table__expand-icon{visibility:hidden;}
</style>